@import "../base/mixins.scss";
@import "../base/vars.scss";

.promo {
  padding: 228px 0 275px;

  background-color: $color_blue_tangaroa;
  background-image: url("../img/bg-header-desktop@1x.png"), url("../img/bg-header-delta@1x.png");
  background-repeat: no-repeat, no-repeat;
  background-position: center, center;
  background-size: cover, cover;

  @media (max-width: $width_viewport_tablet) {
    padding: 115px 0 163px;
  }

  @media (max-width: $width_viewport_mobile) {
    padding: 93px 0 70px;
  }

  @media (min-resolution: $retina_dpi) and (min-resolution: $retina_dppx) {
    background-color: $color_blue_tangaroa;
    background-image: url("../img/bg-header-desktop@2x.png"), url("../img/bg-header-delta@2x.png");
    background-repeat: no-repeat, no-repeat;
    background-position: center, center;
    background-size: cover, cover;
  }

  @media (max-width: $width_viewport_tablet) {
    background-color: $color_blue_tangaroa;
    background-image: url("../img/bg-header-tablet@1x.png"), url("../img/bg-header-delta@1x.png");
    background-repeat: no-repeat, no-repeat;
    background-position: center, center bottom 123px;
    background-size: cover, 197%;
  }

  @media (max-width: $width_viewport_tablet) and (min-resolution: $retina_dpi) and (min-resolution: $retina_dppx) {
    background-color: $color_blue_tangaroa;
    background-image: url("../img/bg-header-tablet@2x.png"), url("../img/bg-header-delta@2x.png");
    background-repeat: no-repeat, no-repeat;
    background-position: center, center bottom 123px;
    background-size: cover, 197%;
  }

  @media (max-width: $width_viewport_mobile) {
    background-color: $color_blue_tangaroa;
    background-image: url("../img/bg-header-mobile@1x.png"), url("../img/bg-header-delta@1x.png");
    background-repeat: no-repeat, no-repeat;
    background-position: center, right -488px bottom 130px;
    background-size: cover, cover;
  }

  @media (max-width: $width_viewport_mobile) and (min-resolution: $retina_dpi) and (min-resolution: $retina_dppx) {
    background-color: $color_blue_tangaroa;
    background-image: url("../img/bg-header-mobile@2x.png"), url("../img/bg-header-delta@2x.png");
    background-repeat: no-repeat, no-repeat;
    background-position: center, right -488px bottom 130px;
    background-size: cover, cover;
  }
}

.promo__inner {
  @include inner;
}

.promo__container {
  display: flex;
  justify-content: space-between;

  @media (max-width: $width_viewport_mobile) {
    flex-direction: column;
  }
}

.promo__content {
  flex-basis: auto;
  width: 50%;
  padding: 85px 0 0;

  @media (max-width: $width_viewport_tablet) {
    width: auto;
    padding: 73px 0 0 62px;
  }

  @media (max-width: $width_viewport_mobile) {
    padding: 73px 0 0;
  }

  & p {
    @include indent_p;

    margin: 0;
    padding: 0 0 30px;

    font-weight: $font_weight_bold;
    font-size: $font_size_p_desktop_xxl;
    line-height: $line_height_p_desktop_xl;
    font-family: $font_family_second_base;
    color: blue;
    letter-spacing: 0.07em;

    @media (max-width: $width_viewport_tablet) {
      padding: 0 0 32px;

      font-size: $font_size_p_tablet_xxl;
      line-height: $line_height_p_tablet_xxs;
      text-transform: uppercase;
    }

    @media (max-width: $width_viewport_mobile) {
      padding: 0 0 29px;

      font-size: $font_size_p_mobile_xxl;
      line-height: $line_height_p_mobile_xxxs;
    }
  }

  & p + p {
    @include indent_p;

    position: relative;

    margin: 0;
    padding: 0 0 44px 98px;

    font-size: $font_size_p_desktop_l;
    line-height: $line_height_p_desktop_xxxl;
    color: $color_white;

    @media (max-width: $width_viewport_tablet) {
      padding: 0 0 56px 50px;

      font-size: $font_size_p_tablet_l;
      line-height: $line_height_p_tablet_xl;
    }

    @media (max-width: $width_viewport_mobile) {
      padding: 0 0 41px 50px;

      font-size: $font_size_p_mobile;
      line-height: $line_height_p_mobile_xl;
    }

    &::before {
      content: "";
      position: absolute;
      top: 16px;
      left: 4px;

      width: 66px;
      height: 2px;

      background: $color_white;

      @media (max-width: $width_viewport_tablet) {
        top: 15px;
        left: 3px;

        width: 33px;
        height: 2px;
      }
    }
  }
}

.promo__image {
  position: relative;

  flex-basis: auto;
  width: 50%;
  overflow: inherit;

  @media (max-width: $width_viewport_tablet) {
    width: 60%;
    margin: 0 -25px 0 0;
  }

  @media (max-width: $width_viewport_mobile) {
    order: -1;
    width: auto;
    margin: 0 0 120px;
  }

  & img {
    position: absolute;
    top: 31px;
    right: -80px;

    @media (max-width: $width_viewport_tablet) {
      position: static;

      width: 465px;
      height: auto;
    }

    @media (max-width: $width_viewport_mobile) {
      position: absolute;
      top: 0;
      left: 15px;
    }
  }
}
